<template>
  <div v-if="groups.length" class="flex flex-col h-screen">
    <header class="flex justify-between items-center py-2 px-4 border-b">
      <h1 class="text-base font-medium">
        {{ $i18n('save_page_to') }}
      </h1>
    </header>
    <main ref="main" class="overflow-y-auto flex-1">
      <!-- Form -->
      <div v-if="!success && !error">
        <nav v-if="!newGroupDialog" class="flex flex-col p-1.5">
          <!-- List of groups -->
          <div ref="groups">
            <a
              v-for="group in groups"
              :key="group.id"
              class="block py-2 px-3 text-base hover:text-white hover:bg-primary rounded focus:ring cursor-pointer focus:ouline-none Group"
              tabindex="1"
              @click="save(group)"
              @keyup.enter="save(group)"
            >
              {{ group.title }}
            </a>
          </div>
          <hr class="my-2" />
          <sl-button
            ref="saveToNewGroupButton"
            class="w-full"
            size="medium"
            variant="default"
            @click="showNewGroupDialog"
          >
            {{ $i18n('newtab_create_group') }}
          </sl-button>
          <div class="p-1 mt-2 text-xs font-bold text-center text-primary">
            <a class="hover:underline" href="#" @click="editShortcut">Edit keyboard shortcut</a>
          </div>
        </nav>
        <div v-else class="p-3">
          <div class="mb-2">
            <sl-input
              ref="input"
              :placeholder="$i18n('newtab_create_group')"
              type="text"
              :value="groupTitle"
              @input="groupTitle = $event.target.value"
              @keyup.enter="createGroupAndSave"
            ></sl-input>
          </div>
          <div class="flex space-x-2">
            <sl-button class="w-auto" @click="hideNewGroupDialog">
              {{ $i18n('cancel') }}
            </sl-button>
            <sl-button class="w-full" :disabled="!groupTitle" variant="primary" @click="createGroupAndSave">
              {{ $i18n('save') }}
            </sl-button>
          </div>
        </div>
      </div>

      <!-- Success -->
      <div v-else-if="success" class="flex flex-col justify-center items-center space-y-2 w-full h-full">
        <sl-icon class="text-2xl text-primary" name="check-circle"></sl-icon>
        <span>{{ $i18n('newtab_dial_saved') }}</span>
      </div>

      <!-- Error -->
      <div v-else-if="error" class="p-4 text-sm text-center">
        {{ error }}
      </div>
    </main>
  </div>
</template>

<script>
import DatabaseService from '@/services/LocalStorageDatabaseService'
import SyncService from '@/services/SyncService'
import { isChrome, isEdge, isFirefox } from '@/utils/browser'
import resizeImage from '@/utils/resizeImage'

export default {
  data() {
    return {
      activeGroup: 0,
      groups: [],
      groupTitle: '',
      newGroupDialog: false,
      success: false,
      error: null,
      isChrome: isChrome(),
      isEdge: isEdge(),
      isFirefox: isFirefox()
    }
  },

  async mounted() {
    this.groups = await DatabaseService.getGroups()
    this.$nextTick(() => {
      const containerHeight = this.$refs.main.scrollHeight + 45
      const minHeight = Math.max(containerHeight, 140)
      const height = Math.min(minHeight, 600)
      document.documentElement.style.minHeight = `${height}px`
    })
    window.addEventListener('keydown', this.keyboardListener)
  },

  unmounted() {
    window.removeEventListener('keydown', this.keyboardListener)
  },

  methods: {
    async saveCurrentTab(groupId = 0) {
      const tabs = await browser.tabs.query({ windowId: browser.windows.WINDOW_ID_CURRENT, active: true })

      if (!tabs || !tabs[0]) {
        throw new Error('No active tabs')
      }

      const { url, title, favIconUrl } = tabs[0]

      // Test if the url is valid
      new URL(url)

      const result = await DatabaseService.createBookmark({ title: title, url: url, idgroup: groupId })

      // Sync
      SyncService.addBookmark(result)

      // Thumbnail
      const base64 = await browser.tabs.captureVisibleTab(browser.windows.WINDOW_ID_CURRENT)
      const image = await resizeImage(base64)
      await DatabaseService.addThumbnail({ id: result?.id, url, image })

      return true
    },

    async saveCurrentTabToNewGroup(group) {
      const result = await DatabaseService.createGroup(group)
      // Sync
      SyncService.addGroup(result)

      // Save bookmark
      return this.saveCurrentTab(result.id)
    },

    async save(group) {
      if (this.isFirefox) {
        try {
          await browser.permissions.request({ origins: ['<all_urls>'] })
        } catch {
          //
        }
      }

      try {
        this.success = await this.saveCurrentTab(group.id)
        setTimeout(() => {
          window.close()
        }, 1000)
      } catch (e) {
        this.error = e.message
      }
    },

    async createGroupAndSave() {
      if (!this.groupTitle) {
        return
      }

      try {
        this.success = await this.saveCurrentTabToNewGroup({ title: this.groupTitle })
      } catch (e) {
        this.error = e.message
      }
    },

    showNewGroupDialog() {
      this.newGroupDialog = true
      this.$nextTick(() => {
        this.$refs.input.focus()
      })
    },

    hideNewGroupDialog() {
      this.newGroupDialog = false
    },

    keyboardListener(e) {
      // no keyboard event action on input select and textarea
      if (
        e.target instanceof HTMLInputElement === true ||
        e.target instanceof HTMLSelectElement === true ||
        e.target instanceof HTMLTextAreaElement === true
      ) {
        return
      }

      // Keyboard navigation through bookmarks
      if (['ArrowUp', 'ArrowDown'].includes(e.key)) {
        const activeElement = window.document.activeElement

        if (!activeElement) {
          return
        }

        // Place the focus on the first group with arrow down
        if (e.key === 'ArrowDown' && activeElement instanceof HTMLBodyElement) {
          this.$refs.groups.children[0].focus()
          e.preventDefault()
          return
        }

        // Place the focus back to bookmarks from the new group button
        if (activeElement == this.$refs.saveToNewGroupButton && e.key === 'ArrowUp') {
          this.$refs.groups.children[this.groups.length - 1]?.focus()
          e.preventDefault()
          return
        }

        // Navigate through groups
        if (activeElement.className.indexOf('Group') > -1) {
          const currentIndex = [...this.$refs.groups.children].indexOf(activeElement)
          const lastIndex = this.$refs.groups.children.length - 1

          // Focus the new group button
          if (currentIndex == lastIndex && e.key === 'ArrowDown') {
            this.$refs.saveToNewGroupButton.focus()
            e.preventDefault()
            return
          }

          // Focus next/previous item
          if (currentIndex > -1 && currentIndex <= lastIndex) {
            let nextIndex

            if (e.key === 'ArrowDown') {
              nextIndex = Math.min(this.groups.length - 1, currentIndex + 1)
            }
            if (e.key === 'ArrowUp') {
              nextIndex = Math.max(0, currentIndex - 1)
            }

            if (nextIndex > -1) {
              this.$refs.groups.children[nextIndex]?.focus()
              e.preventDefault()
              return
            }
          }
        }
      }
    },

    editShortcut() {
      let url

      if (this.isFirefox) {
        url = 'about:addons'
      } else if (this.isEdge) {
        url = 'edge://extensions/shortcuts'
      } else {
        url = 'chrome://extensions/shortcuts'
      }
      url && browser.tabs.create({ url })
    }
  }
}
</script>
